import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
var Ai = require('./ai.js');

function Square(props) {

  let classes = "square"
  let lineClass = null;
  if(props.lineAngle !== null) {
    lineClass = "line-"+props.lineAngle;
  }
  classes += lineClass ? " "+lineClass : "";

  return (
    <button className={classes}
            onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Reset extends React.Component {

  render() {
    if(!this.props.show) {
      return null;
    }

    return (
      <button className="reset" onClick={this.props.onClick}>
        {this.props.value}
      </button>
    );
  }
}


class Board extends React.Component {

  renderSquare(i) {
    let lineAngle = null;
    if(this.props.winningLine && this.props.winningLine.squares.includes(i)) {
      lineAngle = this.props.winningLine.angle;
    }

    return (
      <Square
        key={i}
        value={this.props.squares[i]}
        lineAngle={lineAngle}
        onClick={() => this.props.onClick(i)}
      />
    );
  }

  renderRow(rowId, cols) {
    return (
      <div key={"row-"+rowId} className="board-row">
        {cols}
      </div>
    );
  }

  render() {

    var rows=[]
    const size = boardSize;
    for (let row = 0; row < size; row++) {
      var cols = [];
      for (let col = 0; col < size; col++) {
        cols.push(this.renderSquare(row*size+col));
      }
      rows.push(this.renderRow(row, cols));
    }

    return (
      <div>
        {rows}
      </div>
    );
  }

  reset() {
    console.log('Reset game');
  }
}

function Points(props) {
  return (
    <div className="points">
      <div className="points-left">
        X: {props.points['X']}
      </div>
      <div className="points-right">
        O: {props.points['O']}
      </div>
    </div>
  );
}

class Game extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      squares: Array(boardSize * boardSize).fill(null),
      xIsNext: true,
      gameEnded: false,
      points: {'X':0, 'O':0}
    };
    this.commitTurn = this.commitTurn.bind(this);
    this.ai = new Ai(boardSize, 'O');

  }

  commitTurn(i) {
    const squares = this.state.squares.slice();
    squares[i] = this.state.xIsNext ? 'X' : 'O';
    let gameEnded = false;
    const winningLine = this.ai.calculateWinner(squares);
    if (winningLine !== null) {
      const points = this.state.points;
      points[winningLine.mark] +=1;
      gameEnded = true;
      this.setState({
        points: points,
        gameEnded:gameEnded,
      });
    } else if(this.ai.isDraw(squares)) {
      gameEnded = true;
    }

    this.setState({
      squares: squares,
      xIsNext: !this.state.xIsNext,
      gameEnded: gameEnded,
    });

  }

  handleClick(i) {
    if (this.state.gameEnded || this.state.squares[i]) {
      return;
    }
    this.commitTurn(i);
  }

  componentDidUpdate() {
    if(!this.state.xIsNext && !this.state.gameEnded)
      this.ai.turn(this.state.squares, this.commitTurn);
  }

  handleReset() {
    const squares = Array(boardSize * boardSize).fill(null);
    this.setState({
      squares: squares,
      gameEnded: false,
    });
  }

  render() {
    let status;
    const winningLine = this.ai.calculateWinner(this.state.squares);
    if (winningLine !== null) {
      if(winningLine.mark !== this.ai.mark)
        status = 'Voitit';
      else
        status = 'Hvisit';
    } else if(this.ai.isDraw(this.state.squares)) {
      status = 'Tasapeli'
    } else {
      status = 'Vuoro: ' + (this.state.xIsNext ? 'X' : 'O');
    }

    return (

      <div className="game">
        <Points
          points={this.state.points}
        />
        <div className="game-info">
          <div>{status}</div>
        </div>
        <div>
          <div className="game-board">
            <Board
              squares={this.state.squares}
              winningLine={winningLine}
              onClick={(i) => this.handleClick(i)}
            />
          </div>
          <Reset className="reset"
            value='Uusi peli'
            show={this.state.gameEnded}
            onClick={() => this.handleReset()}
          />
        </div>
      </div>
    );
  }
}

// ========================================
const boardSize = 3;

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);



